<template>
  <div ref="content" class="topHeader">
    <b-row>
      <b-col class="mt-1 cancelCase">
        <b-button @click="close" class="back-btn" variant="primary">
          {{ $t("CLOSE") }}
        </b-button>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-card no-body class="p-1 mt-1 inquary-box mb-2">
          <b-card-body>
            <b-row>
              <b-col class="text-center title-fw title-fs">
                {{ $t("INQUIRY") }}
              </b-col>
            </b-row>
            <br />

            <b-form @submit.prevent="submit">
              <div class="mb-4">
                <b-form-group>
                  <b-row>
                    <b-col cols="5" sm="4" lg="2"></b-col>
                    <b-col cols="5" sm="4" lg="3">
                      <div class="line-3">{{ $t("COMPANYNAMEORNAME") }}</div>
                    </b-col>
                    <b-col cols="2" sm="2" lg="1" class="p-top-5">
                      <b-button
                        variant="primary"
                        class="px-4 required-btn float-right"
                        >{{ $t("REQUIREDBUTTON") }}</b-button
                      >
                    </b-col>
                    <b-col cols="12" sm="12" lg="4">
                      <b-input-group class="mb-1 mt-1">
                        <b-input
                          id="input-id"
                          v-model.trim="$v.inquary.company_name.$model"
                          :placeholder="$t('COMPANYNAMEORNAME')"
                          type="text"
                          class="form-control"
                        />
                      </b-input-group>
                      <show-errors
                        :control="$v.inquary.company_name"
                        :field="$t('COMPANYNAMEORNAME')"
                      />
                    </b-col>
                  </b-row>
                </b-form-group>

                <b-form-group>
                  <b-row>
                    <b-col cols="5" sm="4" lg="2"></b-col>
                    <b-col cols="5" sm="4" lg="3">
                      <div class="line-3">{{ $t("PERSONINCHARGE") }}</div>
                    </b-col>
                    <b-col cols="2" sm="2" lg="1" class="p-top-5">
                      <b-button
                        variant="primary"
                        class="px-4 required-btn float-right"
                        >{{ $t("REQUIREDBUTTON") }}</b-button
                      >
                    </b-col>
                    <b-col cols="12" sm="12" lg="4">
                      <b-input-group class="mb-1 mt-1">
                        <b-input
                          id="input-id"
                          v-model.trim="$v.inquary.person_in_charge.$model"
                          :placeholder="$t('PERSONINCHARGE')"
                          type="text"
                          class="form-control"
                        />
                      </b-input-group>
                      <show-errors
                        :control="$v.inquary.person_in_charge"
                        :field="$t('PERSONINCHARGE')"
                      />
                    </b-col>
                  </b-row>
                </b-form-group>

                <b-form-group>
                  <b-row>
                    <b-col cols="5" sm="4" lg="2"></b-col>
                    <b-col cols="5" sm="4" lg="3">
                      <div class="line-3">{{ $t("PHONENO") }}</div>
                    </b-col>
                    <b-col cols="2" sm="2" lg="1" class="p-top-5">
                      <b-button
                        variant="primary"
                        class="px-4 required-btn float-right"
                        >{{ $t("REQUIREDBUTTON") }}</b-button
                      >
                    </b-col>
                    <b-col cols="12" sm="12" lg="4">
                      <b-input-group class="mb-1 mt-1">
                        <b-input
                          id="input-id"
                          v-model.trim="$v.inquary.phone_no.$model"
                          :placeholder="$t('PHONENO')"
                          type="text"
                          class="form-control"
                          maxlength="11"
                          onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
                        />
                      </b-input-group>
                      <show-errors
                        :control="$v.inquary.phone_no"
                        :field="$t('PHONENO')"
                      />
                    </b-col>
                  </b-row>
                </b-form-group>

                <b-form-group>
                  <b-row>
                    <b-col cols="5" sm="4" lg="2"></b-col>
                    <b-col cols="5" sm="4" lg="3">
                      <div class="line-3">{{ $t("MAILADDRESS") }}</div>
                    </b-col>
                    <b-col cols="2" sm="2" lg="1" class="p-top-5">
                      <b-button
                        variant="primary"
                        class="px-4 required-btn float-right"
                        >{{ $t("REQUIREDBUTTON") }}</b-button
                      >
                    </b-col>
                    <b-col cols="12" sm="12" lg="4">
                      <b-input-group class="mb-1 mt-1">
                        <b-input
                          id="input-id"
                          v-model.trim="$v.inquary.email.$model"
                          :placeholder="$t('MAILADDRESS')"
                          type="text"
                          class="form-control"
                        />
                      </b-input-group>
                      <show-errors
                        :control="$v.inquary.email"
                        :field="$t('MAILADDRESS')"
                      />
                    </b-col>
                  </b-row>
                </b-form-group>

                <b-form-group class="inquary_content">
                  <b-row class="outer">
                    <b-col cols="5" sm="4" lg="2"></b-col>
                    <b-col cols="5" sm="4" lg="3">
                      <div class="textarea-line">
                        {{ $t("CONTENTOFINQUARY") }}
                      </div>
                    </b-col>
                    <b-col cols="2" sm="2" lg="1" class="textarea-pt">
                      <b-button
                        variant="primary"
                        class="px-4 required-btn float-right"
                        >{{ $t("REQUIREDBUTTON") }}</b-button
                      >
                    </b-col>
                    <b-col cols="12" sm="12" lg="4">
                      <b-form-textarea
                        :plain="true"
                        v-model.trim="$v.inquary.content_of_inquary.$model"
                        rows="6"
                        class="f-textarea"
                      >
                        {{}}
                      </b-form-textarea>
                      <show-errors
                        :control="$v.inquary.content_of_inquary"
                        :field="$t('CONTENTOFINQUARY')"
                      />
                    </b-col>
                  </b-row>
                </b-form-group>
              </div>

              <b-row>
                <b-col cols="12" class="text-center mb-1">
                  <b-button
                    variant="primary"
                    type="submit"
                    class="px-4 send-btn"
                  >
                    {{ $t("SEND") }}
                    <i v-if="loading" class="fa fa-spinner fa-spin" />
                  </b-button>
                </b-col>
              </b-row>
            </b-form>
          </b-card-body>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { required, email, numeric, maxLength } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Settings } from "luxon";
import i18n from "@/plugins/i18n";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  components: { ShowErrors },
  data() {
    return {
      session_id: "",
      inquary: {
        company_name: "",
        person_in_charge: "",
        phone_no: "",
        email: "",
        content_of_inquary: "",
      },
      loading: false,
      from_time: "10時",
      to_time: "17時",
    };
  },
  validations: {
    inquary: {
      company_name: { required },
      person_in_charge: { required },
      phone_no: {
        required,
        phoneNumber: numeric,
        phoneMaxLenth: maxLength(11),
      },
      email: { required, email },
      content_of_inquary: { required },
    },
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
    }),
  },
  mounted() {
    this.getUserDataForContactInquiry();
  },
  created() {},
  destroyed() {},

  methods: {
    /**Function to submit inquiry client data and information sending process to API */
    async submit() {
      this.$v.inquary.$touch();
      if (!this.$v.inquary.$invalid) {
        this.loading = true;
        const { data } = await BasicInfoApis.postAPI(
          endPoint.ROOTSENDCONTACTREQUEST,
          {
            client_no: this.user_details.client_account_no,
            company_name: this.inquary.company_name,
            person_incharge: this.inquary.person_in_charge,
            phone_no: this.inquary.phone_no,
            email: this.inquary.email,
            content: this.inquary.content_of_inquary,
          }
        );
        this.loading = false;
        if (data.success) {
          let successHtml = this.getSuccessHtml();
          this.$swal({
            width: 500,
            type: "success",
            text: data.message,
            html: successHtml,
            customClass: {
              popup: "format-pre",
            },
          }).then(() => {});
        } else {
          this.$swal({ type: "warning", text: data.message });
        }
      }
    },
    /**Function to compile successful html message for popup display */
    getSuccessHtml() {
      let successHtml =
        `<div class="title-fw text-left"> ` +
        i18n.t("MSGSENTTOADMIN") +
        ` </div>`;
      successHtml +=
        `<div class="title-fw mb-4 text-left">` +
        i18n.t("WAITFORANSWER") +
        `</div>`;
      successHtml +=
        `<div class="title-fw text-left"> ` +
        i18n.t("ANSWERNEXTDAY") +
        `</div>`;
      successHtml +=
        `<div class="title-fw mb-4 text-left">` +
        i18n.t("FORGIVEME") +
        `</div>`;
      successHtml +=
        `<div class="title-fw text-left">` + i18n.t("SUPPORTTIME") + `</div>`;
      successHtml +=
        `<div class="title-fw text-left">` +
        i18n.t("WEEKDAYS") +
        `：<span>` +
        this.from_time +
        `~` +
        this.to_time +
        `</span></div>`;
      successHtml +=
        `<div class="title-fw text-left">` + i18n.t("CLOSED") + `</div>`;
      return successHtml;
    },
    /**Function to fetch inquiring client data and display it in fields */
    async getUserDataForContactInquiry() {
      this.session_id = localStorage.getItem("client_account_no");
      const { data } = await BasicInfoApis.postAPI(endPoint.CONTACT, {
        client_account_no: this.session_id,
      });
      this.inquary = {
        company_name: data.data.client_name,
        person_in_charge: data.data.client_person_name,
        phone_no: data.data.client_tel,
        email: data.data.client_mail,
        content_of_inquary: "",
      };
    },

    close() {
      window.close();
    },
  },
};
</script>

<style scoped lang="scss">
.card {
  border: 0px;
}
.inquary-box {
  margin: 0% 20% 0% 20%;
  min-height: 74vh;
}
.inquary-box .card-body {
  padding-top: 0rem;
}

.form-group.inquary_content {
  margin-bottom: 0.5rem;
}
.f-textarea {
  max-height: 250px;
  overflow-y: scroll;
}
.textarea-pt {
  padding: 0.55rem 0rem;
}
.textarea-line {
  line-height: 2.4;
}
.outer {
  display: flex;
  align-items: center;
}
.swal2-content {
  text-align: left;
}
.topHeader{
  margin-top: 55px !important;
}

@media only screen and (max-width: 575px) {
  .textarea-pt {
    padding: 0.55rem 0rem;
  }
  .textarea-line {
    line-height: 2.4;
  }
}
</style>
